.html {
  font-size: 100px;
  font-family: Arial, sans-serif;
}

.body {
  margin: 0;
  padding: 0;
}

.container {
  display: flex;
  flex-wrap: wrap;
}

.block {
  height: 300px;
  flex-basis: 300px;
  flex-grow: 1;
  flex-shrink: 0;
  color: #fff;
  text-align: center;
  line-height: 300px;
}

.block--1 {
  background-color: rgb(255, 0, 0);
  order: 1;
}

.block--2 {
  background-color: rgb(204, 0, 0);
  order: 2;
}

.block--3 {
  background-color: rgb(153, 0, 0);
  order: 3;
}

.block--4 {
  background-color: rgb(102, 0, 0);
  order: 4;
}

.block--5 {
  background-color: rgb(51, 0, 0);
  order: 5;
}

.block--6 {
  background-color: rgb(0, 0, 0);
  order: 6;
}

@media (min-width: 600px) {
  .block {
    flex-basis: 50%;
  }

  .block--3 {
    order: 4;
  }

  .block--4 {
    order: 3;
  }
}

@media (min-width: 900px) {
  .block {
    flex-basis: calc(100% / 3);
  }

  .block--4 {
    order: 6;
  }

  .block--6 {
    order: 4;
  }
}
